import React,{Component} from 'react';
import Dropdown from '../Dropdowns/Dropdown/Dropdown';
import {Container,Row,Col} from 'react-bootstrap';
import CreatableMulti from './Dropdown/CreateSelectDropdown';

class Dropdowns extends Component {
    state={
   Diet : [
  { value: 'chocolate', label: 'Chocolate' },
  { value: 'strawberry', label: 'Strawberry' },
  { value: 'vanilla', label: 'Vanilla' }
],
Intolerance : [
    { value: 'chocolate', label: 'Dairy' },
    { value: 'strawberry', label: 'Egg' },
    { value: 'vanilla', label: 'Grain' },
    { value: 'chocolate', label: 'Peanut' },
    { value: 'strawberry', label: 'Seafood' },
    { value: 'vanilla', label: 'Sesame' },
    { value: 'chocolate', label: 'Shellfish' },
    { value: 'strawberry', label: 'Soy' },
    { value: 'vanilla', label: 'Sulfite' },
    { value: 'strawberry', label: 'Tree Nut' },
    { value: 'vanilla', label: 'Wheat' },
  ],
  Type : [
    { value: 'main course', label: 'Main Course' },
    { value: 'side dish', label: 'Side Dish' },
    { value: 'dessert', label: 'Dessert' },
    { value: 'main course', label: 'Appetizer' },
    { value: 'strawberry', label: 'Salad' },
    { value: 'vanilla', label: 'Bread' },
    { value: 'dessert', label: 'BreakFast' },
    { value: 'main course', label: 'Soup' },
    { value: 'strawberry', label: 'Beverage' },
    { value: 'vanilla', label: 'Sauce' },
    { value: 'dessert', label: 'Marinade' },
    { value: 'main course', label: 'FingerFood' },
    { value: 'strawberry', label: 'Snack' },
    { value: 'vanilla', label: 'Drink' },
  ],
  Cuisine : ["African",'American','British','Cajun','Caribbean','Chinese','Eastern European','European',
           'French','German','Greek','Indian','Irish','Italian','Japanese','Jewish','Korean','Latin American',
           'Mediterranean','Mexican','middle Eastern','Nordic','Southern','Thai','Vietnamese'].map(el=>{
               return(
                   {value:el,label:el}
               );
           })
}

  render() {
    return (
        <Container>
        <Row className={`my-4 justify-content-center 3`}>
          <Col xs={12} md={3} className={`my-3`}><Dropdown placeholder='Filter Diet' options={this.state.options}></Dropdown></Col>
          <Col xs={12} md={3} className={`my-3`}><Dropdown placeholder='Filter Intolerance' options={this.state.options}></Dropdown></Col>
          <Col xs={12} md={3} className={`my-3`}><Dropdown placeholder='Filter Type' options={this.state.Type}></Dropdown></Col>
          <Col xs={12} md={3} className={`my-3`}><CreatableMulti placeholder='Exclude Cuisine' options={this.state.Cuisine}></CreatableMulti></Col>
        </Row>
      </Container>
    );
  };
  
};
export default Dropdowns;