// @flow 
import * as React from 'react';
import ClassStyles from './Modal.module.css';
import Backdrop from '../Backdrop/Backdrop';
const Modal = (props) =>(
    props.show?
    (
        <Backdrop className={`bg-danger`} close={props.close}>
            <div className={`bg-light rounded h-90 p-3 ${ClassStyles.child}`} style={{color:'#1D1B6E'}}>
              {props.children}
            </div>
       </Backdrop>
    ):null
);
export default Modal;