import React, { Component } from 'react';
import ClassStyles from './Layout.module.css';
import Nav from '../../Components/Nav/Nav';
import Header from '../../Components/Header/Header';
import Aux from '../../Hoc/Auxilliary';
import Dropdowns from '../../Components/Dropdowns/Dropdowns';
import Recipes from '../../Components/Recipes/Recipes';
import axios from 'axios';
import Backdrop from '../../Components/Backdrop/Backdrop';
import Modal from '../../Components/Modal/Modal';
import RecipeInfo from '../../Components/RecipeInfos/RecipeInfo';
class Layout extends Component{

    state={
        data:{
            RecipeData:[],
            RecipesLoaded:false,
            offset:0,
            number:30,
            loadingMoreRecipes:false,
        } ,
        totalResults:0, 
        initialSearchItem:'',
        showModal:false,
        selectedID:null
        
    };
     moreRecipesHandler= async ()=>{
            let offsetVal=30;
            let numberVal=this.state.data.number+30;
            let newState={...this.state.data}
            newState.number=numberVal
            newState.offset=offsetVal
            newState.loadingMoreRecipes=true;
            await this.setState({data:newState});
            this.fetchRecipes();
        }
    
   
 async changeSearchValHandler(val)
 {
  await this.setState({initialSearchItem:val});
   this.fetchRecipes();
 }

 fetchRecipes(){
    console.log(`inside FectRecipes: ${this.state.initialSearchItem}`);
    let recipeItems=[];
    axios.get(`https://api.spoonacular.com/recipes/complexSearch?apiKey=0198da9be6534f7ea70d9089e498c835&query=${this.state.initialSearchItem}&number=${this.state.data.number}&addRecipeInformation=true&offset=${this.state.data.offset}`)
    .then(response=>{
        response.data.results.forEach(fooditem => {
            let recipeitem={}
            recipeitem={name:fooditem.title,
                     id:fooditem.id,
                     prepTime:fooditem.readyInMinutes,
                     image:fooditem.image,
                     price:fooditem.pricePerServing,
                    rating:fooditem.spoonacularScore,
                    type:fooditem.dishTypes.slice(0,4).join(','),
                    cuisine:fooditem.cuisines.join(','),
                }
                recipeItems.push(recipeitem);
            });
            const newState={...this.state.data}
            newState.RecipeData=recipeItems;
            newState.RecipesLoaded=true;
            newState.loadingMoreRecipes=false;
            this.setState({data:newState,totalResults:response.data.totalResults});      
            console.log(this.state.data);
            console.log(newState);
        }).catch(error=>{
            alert(error);
        });
        
}
toggleModal(){
    const ModalVisibility=this.state.showModal;
    this.setState({showModal:!ModalVisibility});
}

 componentDidMount(){
    this.fetchRecipes();
 }
 async selectedIdHandler(ID)
 {
    await this.setState({selectedID:ID});
    this.toggleModal();
 }


 render(){
     return(
          <Aux>
        {this.state.selectedID!==null?(<Modal show={this.state.showModal} close={this.toggleModal.bind(this)}><RecipeInfo id={this.state.selectedID}></RecipeInfo></Modal>):null}
         <Nav></Nav>
         <Header clicked={this.changeSearchValHandler.bind(this)}></Header>
         <Dropdowns></Dropdowns>
         <Recipes data={this.state.data} totalResults={this.state.totalResults} moreRecipesHandler={this.moreRecipesHandler} clicked={this.selectedIdHandler.bind(this)}></Recipes>
         </Aux>
     ); 
}
}
export default Layout;